import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, FormControl, FormLabel, Input, VStack, Checkbox,
  // HStack,
  Button,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { card } from '../../../../types/card';
import { updateCard } from '../../../../store/middlewares/card';
import { apiErrorHandler } from '../../../../utils/general';
import CurrencyInput from '../../../../components/CurrencyInput';
import { useDrawer } from '../../../../hooks/drawer';

const CardInfo = () => {
  const { t } = useTranslation();
  const { goBack } = useDrawer();
  const dispatch = useAppAsyncDispatch();

  const cardPayloadData = useAppSelector((store) => store.card.selectedCard, shallowEqual);
  const [cardData, setCardData] = useState(cardPayloadData);

  const setPayload = useCallback((key: keyof card, value: any) => {
    if (cardData) {
      const copiedData = {
        ...cardData,
        [key]: value
      };

      setCardData(copiedData);
    }
  }, [cardData]);

  const isValid = useMemo(() => cardData?.limitInterval && cardData?.label.length !== 0 && cardData?.limitAmount, [cardData]);

  const submit = useCallback(() => {
    if (cardData) {
      dispatch(updateCard(cardData.id, {
        label: cardData.label,
        limitAmount: cardData.limitAmount,
        limitInterval: cardData.limitInterval,
      })).then(() => {
        goBack();
      }).catch(apiErrorHandler);
    }
  }, [cardData]);

  return (
    <div className="page">
      <MiniBox
        heading={t(`edit_card_info.title_${cardPayloadData?.cardType}`)}
        onBack={goBack}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
            {t('edit_card_info.ctaText')}
          </Button>
        )}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('edit_card_info.cardLabel')}
            </FormLabel>
            <Input
              value={cardData?.label}
              variant="primary"
              onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPayload('label', e.target.value)}
            />
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('edit_card_info.setSpendingLimit')}
            </FormLabel>
            <CurrencyInput
              value={cardData?.limitAmount || ''}
              onChange={(value) => {
                setPayload('limitAmount', value);
              }}
            />
          </FormControl>

          <VStack alignItems="flex-start" py="24px">
            <Checkbox value="daily" onChange={() => setPayload('limitInterval', 'daily')} isChecked={cardData?.limitInterval === 'daily'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.daily')}</Text>
            </Checkbox>
            <Checkbox value="weekly" onChange={() => setPayload('limitInterval', 'weekly')} isChecked={cardData?.limitInterval === 'weekly'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.perWeek')}</Text>
            </Checkbox>
            <Checkbox value="monthly" onChange={() => setPayload('limitInterval', 'monthly')} isChecked={cardData?.limitInterval === 'monthly'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.perMonth')}</Text>
            </Checkbox>
            <Checkbox value="yearly" onChange={() => setPayload('limitInterval', 'yearly')} isChecked={cardData?.limitInterval === 'yearly'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.perYear')}</Text>
            </Checkbox>
            <Checkbox value="allTime" onChange={() => setPayload('limitInterval', 'allTime')} isChecked={cardData?.limitInterval === 'allTime'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.allTime')}</Text>
            </Checkbox>
            <Checkbox value="perTransaction" onChange={() => setPayload('limitInterval', 'perTransaction')} isChecked={cardData?.limitInterval === 'perTransaction'}>
              <Text variant="regular" fontSize="14px">{t('edit_card_info.perTransaction')}</Text>
            </Checkbox>
          </VStack>
        </Box>
      </MiniBox>

    </div>
  );
};

export default CardInfo;
